@use "./mixin" as m;
.comp-map {
  @media screen and (max-width: 900px) {
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0 !important;
    transform: translateX(100%);
    transition: transform 0.5s;
    &.active {
      transform: translateX(0);
    }
  } //comp-map max 900
  .btns {
    position: absolute;
    display: flex;
    top: calc(50% - 25px);
    left: -50px;
    width: 100px;
    height: 50px;
    z-index: 1;
    border-radius: 25px;
    overflow: hidden;
    button {
      width: 50px;
      height: 50px;
      color: #fff;
      &:nth-child(1) {
        background: rgba(red, 0.7);
      }
      &:nth-child(2) {
        background: rgba(orange, 0.5);
      }
    } //button
    @media screen and (min-width: 900px) {
      display: none;
    }
  } //btns
  .section-inner {
    @media screen and (max-width: 900px) {
      height: 100%;
    }
    #map {
      filter: hue-rotate(40deg) brightness(102%);
      height: 100%;
      border-radius: 10px;
    }
  } //inner
} //conp-map
