@use "./mixin" as m;
html {
  --sky: #8ed1f1;
  --color: #41759e;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  position: relative;
  height: 100vh;
  background-color: var(--sky);

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../assets/img/weather-icos.gif);
    background-size: 800px;
    opacity: 0.3;
    animation: bg 3600s infinite linear;
    @keyframes bg {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: -50000px -50000px;
      }
    }
  }
}

h1 {
  z-index: 1;
  perspective: 1000px;
  position: absolute;
  width: min(50%, 200px);
  top: 0;
  left: 50%;
  transform: translate(-50%,-60%);
  img {
    width: 100%;
    opacity: 0.7;
    animation: rotate 10s infinite linear;
  }
  @keyframes rotate{
    0%{transform:rotateX(0deg)}
    30%{transform:rotateX(360deg)}
    100%{transform:rotateX(360deg)}
  }
}

#root {
  @include m.posLT(fixed, 50%, 50%, -50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(#fff, 0.7);
  border-radius: 10px;
  box-sizing: border-box;
  padding: 5px;
  animation: grow 1s infinite alternate linear;
  border:1px solid rgba(#FFF,0.6);
  @media screen and (min-width:900px){
    min-height: 700px;
  }

  @keyframes grow{
    0%{  box-shadow: 0 0 10px 0px rgba(#FFF , 0.5);}
    100%{  box-shadow: 0 0 20px 5px rgba(#FFF , 0.5);}
  }
  .app-inner {
    position: relative;
    height: 100%;
    @media screen and (min-width: 900px) {
      overflow: hidden;
      display: flex;
      flex-wrap: wrap;
    }//min 900
    @media screen and (max-width: 900px) {
      overflow: hidden;
      overflow-y: auto;
    } //max 900
    section{
      box-sizing: border-box;// box-sizing?
      @media screen and (min-width: 900px) {
        width: 50%;
        height: 50%;
      }//min 900
      @media screen and (max-width: 900px) {
      margin:20px 0 ;
      } //max 900
      .section-inner{
        box-sizing: border-box;
        padding: 7px;
        position: relative;
        border-radius: 10px;
        min-height: 300px;
        @media screen and (min-width: 900px) {
          height: 100%;
          overflow: auto;
        }//min 900
        .preloader{
          position: absolute;
          width: 100%;
          height: 100%;
          left:0;
          top:0;
          background: var(--sky);
          display: flex;
          justify-content: center;
          align-items: center;
        }
        h2{
          text-transform: uppercase;
          font-weight: 600;
          padding: 5px 10px;
          background: var(--color);
          color:#FFF;
          border-radius: 5px;
          @media screen and (min-width:900px){
            position: sticky;
            top:0;
            left: 0;
            z-index: 1;
          }
        }
      }//s-inner
    }//section
  } //a-inner
} //root


body,.app-inner , .section-inner {
  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--color);
    }
  } //media hover
}


/* spinner */
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 120px;
  height: 120px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 12px;
  box-sizing: border-box;
  border: 48px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}
