@use "./mixin" as m;

.comp-daily {
  .section-inner {
    padding-top: 0 !important;
    ul {
      li {
        padding: 5px;
        border-bottom: 1px solid rgba(#8ed1f1, 0.7);
        a {
          position: relative;
          display: block;
          padding: 10px 0;
          transition: all 0.5s;
          .fa-arrow-down {
            position: absolute;
            right: 10px;
            top:50%;
            transform: translateY(-50%);
            font-size: 1.3em;
            transition: transform 0.5s;
          }
          .date {
            font-weight: 500;
            margin-bottom: 5px;
            span {
              margin-right: 5px;
              &.desc {
                font-style: italic;
              }
              i {
                margin-right: 5px;
              }
            }
          }//date
          .info {
            img {
              display: inline-block;
              vertical-align: middle;
              margin-right: 10px;
              width: 50px;
            }
            span {
              margin-right: 10px;
              i {
                margin-right: 5px;
              }
            }
          } //info
          &.active{
            background: var(--sky);
            color:#FFF;
            >i{
              transform:translateY(-50%) rotate(180deg);
            }
          }
        } //a
        .detail {
          display: none;
          color: #fff;
          background: rgba(#41759e, 0.6);
          p {
            padding: 10px 5px;
            border: 1px solid #fff;
            &:last-child {
              border: none;
            }
            span {
              margin-right: 15px;
              i {
                font-size: 0.9em;
              }
              b{
                margin:0 5px;
              }
              em {
                font-weight: 500;
                small {
                  font-size: 0.85em;
                }
              }
            }//span
          }//p
        }//detail
      } //li
    } //ul
  } //section-inner
} //comp-daily
