@use "./mixin" as m;

.comp-hourly {
  .section-inner {
    padding-top: 0 !important;
    overflow: auto;
    h2 {
      position: sticky;
      left: 0;
    }
    @media screen and (min-width: 900px) {
      display: flex;
      flex-direction: column;
    } //section-inner min

    .wrap {
      display: inline-table;
      position: relative;
      @media screen and (min-width: 900px) {
        height: 100%;
      } //wrap min
      @media screen and (max-width: 900px) {
        height: 400px;
      } //wrap max
      ul {
        display: inline-flex;
        height: 100%;
        li {
          padding-top: 20px;
          width: 70px;
          height: 100%;
          box-sizing: border-box;
          &:nth-child(even){
            background: rgba(skyblue,0.2);
          }
          p{
            font-size: 0.9em;
            text-align: center;
            margin: 8px 0;
            img{
              display: inline-block;
              width: 70%;
            }
          }//p
        }//li
      }//ul
      .chart-wrap {
        position: absolute;
        width: calc(100% - 35px);
        height: 50%;
        bottom: 0;
        left: 10px;
      }
    } //wrap
  } //section-inner
} //comp-hourly
