@use "./mixin" as m;

section.comp-current {
  @media screen and (max-width: 900px) {
    margin-top: 20px !important;
  } //comp-current min 900
  .section-inner {
    .current-output {
      position: relative;
      box-sizing: border-box;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-size: cover;
      background-position: center;
      color: #fff;
      .reset {
        z-index: 3;
        position: absolute;
        width: 50px;
        height: 50px;
        top: 15%;
        right: 5%;
        font-size: 1.5em;
        opacity: 0.7;
        transition: opacity 0.1s;
        animation: rota 5s infinite linear;
        &:active,
        &:hover {
          opacity: 1;
        }
      }
      @media screen and (min-width: 900px) {
        height: 100%;
      } //current-output min 900
      @media screen and (max-width: 900px) {
        padding: 10px;
      } //current-output max 900
      address {
        display: flex;
        overflow: hidden;
        margin-bottom: 15px;
        width: min(90%, 350px);
        padding: 3px;
        box-sizing: border-box;
        background: rgba(#000, 0.2);
        span {
          display: inline-block;
          white-space: nowrap;
          animation: marquee 10s infinite linear;
          font-size: 1.1em;
          font-weight: 600;
          @keyframes marquee {
            0% {
              transform: translateX(0%);
            }
            100% {
              transform: translateX(-100%);
            }
          }
        }
      }
      .circle-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        width: min(85%, 300px);
        border-radius: 50%;
        background: rgba(#000, 0.2);
        @include m.square;
        .sunset {
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg {
            width: 100%;
            .sun {
              fill: rgba(245, 209, 0);
              offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
              /* offset-distance: */
              animation: rota 3s infinite linear;
              @keyframes rota {
                0% {
                  transform: rotate(0) scale(0.5);
                }
                25% {
                  transform: rotate(90deg) scale(0.6);
                }
                50% {
                  transform: rotate(180deg) scale(0.7);
                }
                75% {
                  transform: rotate(270deg) scale(0.6);
                }
                100% {
                  transform: rotate(360deg) scale(0.5);
                }
              }
            }
            .line {
              fill: none;
              stroke: rgba(#fff, 0.9);
              stroke-width: 3px;
            }
            .dash {
              fill: none;
              stroke: rgba(245, 209, 0);
              stroke-width: 6px;
              stroke-dasharray: 1;
              /* stroke-dashoffset: 1; */
            }
          }
          time {
            display: inline-block;
            font-size: 0.85em;
            padding: 4px 7px;
            background: rgba(245, 209, 0);
            border-radius: 15px;
            &.rise-time {
              transform: translateX(-30%);
            }
            &.set-time {
              float: right;
              transform: translateX(30%);
            }
          }
        } //sunset

        .circle-container-inner {
          @include m.posLT(absolute, 50%, 50%, -50%, -50%);
          width: 90%;
          .temp {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            span {
              position: absolute;
              transform: translate(40px, 15px);
              i {
                margin-right: 3px;
                font-weight: 600;
              }
            }
            em {
              font-size: 2em;
              margin-right: -70px;
              position: relative; //??
              z-index: 1;
              text-shadow: 1px 1px 0 var(--color);
              sup {
                font-size: 0.9em;
              }
            }
            img {
              width: 100px;
              mask-image: url(../assets/img/mask.png);
              mask-size: 100%;
              mask-position: center;
              mask-repeat: no-repeat;
            }
          } //temp
          .desc {
            margin-top: -20px;
            text-align: center;
            font-size: 1.2em;
            i {
              font-size: 0.8em;
              margin-right: 3px;
            }
          } //desc
          .etc {
            width: 70%;
            margin: 7px auto;
            text-align: center;
            box-sizing: border-box;
            padding: 5px;
            border-radius: 15px;
            background: rgba(#fff, 0.4);
            span {
              text-align: center;
              margin: 2px 2px;
              font-size: 0.9em;
              display: inline-block;
              i {
                margin-right: 3px;
                font-size: 0.8em;
                &.fa-location-arrow {
                  margin-right: 5px;
                }
              }
              b {
                font-size: 0.8em;
              }
              small {
              }
            } //span
          } //etc
          .time-date {
            text-align: center;
            color: #fff;
            time {
              display: block;
              &.time {
                font-size: 1.1em;
                small{
                  font-size: 0.8em;
                }
              }
              &.date {
                font-size: 0.9em;
              }
            }
          }
        } //circle-container-inner
      } //circlel-container
    } //current-output
  } //section-inner
}
