html {
  --sky: #8ed1f1;
  --color: #41759e;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  position: relative;
  height: 100vh;
  background-color: var(--sky);
}
body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../assets/img/weather-icos.gif);
  background-size: 800px;
  opacity: 0.3;
  animation: bg 3600s infinite linear;
}
@keyframes bg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -50000px -50000px;
  }
}

h1 {
  z-index: 1;
  perspective: 1000px;
  position: absolute;
  width: min(50%, 200px);
  top: 0;
  left: 50%;
  transform: translate(-50%, -60%);
}
h1 img {
  width: 100%;
  opacity: 0.7;
  animation: rotate 10s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotateX(0deg);
  }
  30% {
    transform: rotateX(360deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}

#root {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  box-sizing: border-box;
  padding: 5px;
  animation: grow 1s infinite alternate linear;
  border: 1px solid rgba(255, 255, 255, 0.6);
}
@media screen and (min-width: 900px) {
  #root {
    min-height: 700px;
  }
}
@keyframes grow {
  0% {
    box-shadow: 0 0 10px 0px rgba(255, 255, 255, 0.5);
  }
  100% {
    box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.5);
  }
}
#root .app-inner {
  position: relative;
  height: 100%;
}
@media screen and (min-width: 900px) {
  #root .app-inner {
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner {
    overflow: hidden;
    overflow-y: auto;
  }
}
#root .app-inner section {
  box-sizing: border-box;
}
@media screen and (min-width: 900px) {
  #root .app-inner section {
    width: 50%;
    height: 50%;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner section {
    margin: 20px 0;
  }
}
#root .app-inner section .section-inner {
  box-sizing: border-box;
  padding: 7px;
  position: relative;
  border-radius: 10px;
  min-height: 300px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner {
    height: 100%;
    overflow: auto;
  }
}
#root .app-inner section .section-inner .preloader {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: var(--sky);
  display: flex;
  justify-content: center;
  align-items: center;
}
#root .app-inner section .section-inner h2 {
  text-transform: uppercase;
  font-weight: 600;
  padding: 5px 10px;
  background: var(--color);
  color: #FFF;
  border-radius: 5px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner h2 {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
  }
}

@media (hover: hover) {
  body::-webkit-scrollbar-track, .app-inner::-webkit-scrollbar-track, .section-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  body::-webkit-scrollbar, .app-inner::-webkit-scrollbar, .section-inner::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  body::-webkit-scrollbar-thumb, .app-inner::-webkit-scrollbar-thumb, .section-inner::-webkit-scrollbar-thumb {
    background-color: var(--color);
  }
}

/* spinner */
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 120px;
  height: 120px;
}

.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 12px;
  box-sizing: border-box;
  border: 48px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-hourglass 1.2s infinite;
}

@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}