@media screen and (max-width: 900px) {
  .comp-map {
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0 !important;
    transform: translateX(100%);
    transition: transform 0.5s;
  }
  .comp-map.active {
    transform: translateX(0);
  }
}
.comp-map .btns {
  position: absolute;
  display: flex;
  top: calc(50% - 25px);
  left: -50px;
  width: 100px;
  height: 50px;
  z-index: 1;
  border-radius: 25px;
  overflow: hidden;
}
.comp-map .btns button {
  width: 50px;
  height: 50px;
  color: #fff;
}
.comp-map .btns button:nth-child(1) {
  background: rgba(255, 0, 0, 0.7);
}
.comp-map .btns button:nth-child(2) {
  background: rgba(255, 165, 0, 0.5);
}
@media screen and (min-width: 900px) {
  .comp-map .btns {
    display: none;
  }
}
@media screen and (max-width: 900px) {
  .comp-map .section-inner {
    height: 100%;
  }
}
.comp-map .section-inner #map {
  filter: hue-rotate(40deg) brightness(102%);
  height: 100%;
  border-radius: 10px;
}